import React, { useContext } from 'react';
import "../styles/cart.css";
import { CartContext} from '../CartContext';
import { TotalContext } from'../TotalContext';
import axios from 'axios';

const Cart = () => {
    
    const [cart, setCart] = useContext(CartContext); 
    const [total, setTotal] = useContext(TotalContext);
    
    const handleDel = (id) => {

        const newCart  = cart.filter((i) => i.id !== id);
        setCart(newCart);
        localStorage.setItem('cart', JSON.stringify(cart));
        
        setTotal(total);
        if (cart.length === 0) {document.querySelector('.cart-body').classList.toggle('hide')}
    }

    const handleOrder = async () => {
        const newTry = {
            order: [...cart],
            date: new Date(),
            complete: false,
        }
        await axios.post("https://feolas.herokuapp.com/add", newTry).then(res => console.log(res.data));

        setCart('');
        setTotal(0);
        document.querySelector('.cart-body').classList.toggle('cart-hide')
    }


    return (
        <div className="shop-cart">
            <div className="cart-button"  data-title="Корзина заказов" onClick={() => document.querySelector('.cart-body').classList.toggle('cart-hide')} ></div>
            <div className="cart-body">
            <ul className="cart-lister" >
                {cart.length ? cart.map((i) => {
                    return(
                    <li key={i.id}>
                        <span className="name">{i.name}</span>
                        <span className='val'>
                            <span className="minus" onClick={() => {
                               if (i.val > 1) {

                                i.val--
                                localStorage.setItem('cart', JSON.stringify(cart));
                                console.log(i.val);
                                setCart(JSON.parse(localStorage.getItem("cart")))
                               }
                                }}>-</span>
                            <span id={i._id} className='inputs'>{i.val}</span>
                            <span className="plus" onClick={() => {
                                i.val++;
                                localStorage.setItem('cart', JSON.stringify(cart))
                                console.log(i.val);
                                setCart(JSON.parse(localStorage.getItem("cart")))
                            }
                            }>+</span>
                        </span>
                        <span className="cost" >{i.price * i.val}</span>
                        <span className="del" id={i._id} onClick={() => handleDel(i.id)}>x</span>
                    </li>
                    )
                }) : console.log('cart is empty')}  
            </ul>
            <div className="total-wrap"><span className="total-text">Сумма заказа: {total}</span><span className={total > 0 ? 'total' : 'hide'} onClick={() => handleOrder() }></span></div>

            </div>
            
        </div>
    )
}

export default Cart
