import React, { useState, useContext, useEffect } from 'react';
import axios from 'axios';
import '../styles/admin.css'
import { MenuContext } from '../MenuContext';

const Admin = () => {
    const [name, setName] = useState('');
    const [desc, setDesc] = useState('');
    const [price, setPrice] = useState('');
    const [category, setCat] = useState('');
    const [photo, setPhoto] = useState('');
    const [menu] = useContext(MenuContext);
    const [remove, setRemove] = useState('');
    const [orders, setOrders] = useState([]);

    const isCompleteCkecker = async () => {
        const allOrderItems = [...document.querySelectorAll(".bordered")];
        const test = orders.filter(i => i.complete === true);
        const needed = test.map((i) => allOrderItems.filter(o => o.id === i._id));
        await needed.forEach(el => el[0].classList.add("complete-order")); 
    }
    
    useEffect(() => {
        const fetchOrders = async () => {
        const requestOrders = await axios.get('https://feolas.herokuapp.com/orders');
        setOrders(requestOrders.data);
        }
        fetchOrders();
    }, [])

    useEffect(() => isCompleteCkecker())
    
    const handleSubmit = (e) => {
        e.preventDefault();
                const dishItem = {
                    name,
                    desc,
                    price,
                    category,
                    photo_url: photo,
                };
        
        axios.post('https://feolas.herokuapp.com/admin', dishItem)
        .then(res => console.log(res.data))

        setName('');
        setDesc('');
        setPrice('');
        setCat('');
        setPhoto('');
    }
    const handleRemove = () => {
        axios.delete(`https://feolas.herokuapp.com/menu/` + remove);
        setRemove('');
    }
    const handleHideOrders = () => {
        const removeCompletedItems = orders.filter(i => i.complete !== true);
        setOrders(removeCompletedItems);
    }

    const handleRemoveOrders = () => {
        const ordersToDelete = orders.filter(i => i.complete === true);
        const warning = window.confirm('Вы уверенны, что хотите НАВСЕГДА удалить из Базы Данных отмеченные Заказы?');
         
        if (warning === true) {
            ordersToDelete.forEach(order => axios.delete('https://feolas.herokuapp.com/orders/'+ order._id ))
            handleHideOrders()
        } 
    }

    return (
        <div className="admin-page">

        <div className="add-section">
          <h1>Add new dish:</h1>
          <form action="" className="add-form" onSubmit={(e) => handleSubmit(e)} >
            <label >Ссылка на фото: <input value={photo}  type="text" onChange={(e) => setPhoto(e.target.value)} /></label>
            <label>Название: <input value={name} required type="text" onChange={(e) => setName(e.target.value)} /></label>
            <label>Описание: <input value={desc} required type="text"  onChange={(e) => setDesc(e.target.value)}/></label>
            <label>Цена: <input value={price} required type="text"  onChange={(e) => setPrice(e.target.value)} /></label>
            <select id="categories" onChange={(e) => setCat(e.target.value)}>
                <option defaultValue>Выбрать категорию:</option>
                <option value="firstdishes" >Первое блюдо</option>
                <option value='seconddishes' >Горячее</option>
                <option value="salades">Салаты или закуски</option>
                <option value="deserts">Десерт</option>
                <option value="kids">Детское меню</option>
            </select>
            <input type="submit" className="hidden-element"/>
          </form>
          <h1>Delete dish:</h1>
          <div className="menu-list-admin">
              <select onChange={(e) => setRemove(e.target.value)}>
                  <option defaultValue>Выберите удаляемое блюдо:</option>
              {menu.map(i => {
                  return(
                    <option value={i._id} key={i._id}>{i.name}</option>
                  )
              })}
              </select>
              <button className="remove" onClick={() => handleRemove()}>Delete!</button>
          </div>
        </div>
        <div className="orders-section">
              <div className="head-sec">
            <h1>Orders:</h1><h5 className="clear-button" onClick={() => handleHideOrders()} >Очистить выполненные</h5>
            </div>
              <div className="order-list">
                  {orders.length ? orders.map((i) => {
                      return(<div className="bordered" id={i._id}  key={i._id}>   
                      <h4 className="order-name">Заказ от {i.date}</h4><span className="complete-button" onClick={async (e) => {
                          i.complete = !i.complete;
                          e.target.parentElement.classList.toggle('complete-order');
                          await axios.post('https://feolas.herokuapp.com/or/' + i._id, i )
                          }} >Выполнено</span>
                          <ul className="ordered-item">
                              {i.order.map(ii => {
                                  return(
                                      <li key={ii.id}>
                                          <span>{ii.name}</span>
                                          <span>{ii.val}</span>
                                          <span className={`total-${i._id}`}>{ii.val * ii.price}</span>
                                      </li>
                                  )
                              })}
                          </ul>
                          <div className="consum">Итого: {[...document.querySelectorAll(`.total-${i._id}`)].map(i => i.innerText * 1).reduce((a, b) => a + b, 0)} </div>
                    </div>
                      )
                  }) : <h2>There is no orders today!</h2>}
                  <button className={orders.length ? 'remove-danger' : 'remove-danger hidden-element'} onClick={() => handleRemoveOrders() } >Удалить выполненные из базы данных (!)</button>
              </div>

        </div>
        </div>
    )
}

export default Admin
