import React, { useEffect, useContext } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import "./App.css";
import Admin from "./components/Admin";
import Homepage from "./components/Homepage";
import Navbar from "./components/Navbar";
import Salades from "./components/Salades";
import Seconds from "./components/Seconds";
import SideNav from "./components/SideNav";
import Soupes from "./components/Soupes";
import Deserts from "./components/Deserts";
import Kids from "./components/Kids";
import { CartContext } from "./CartContext";
import { TotalContext } from "./TotalContext";
import { MenuContext } from "./MenuContext";
import axios from "axios";

function App() {
  const [menu, setMenu] = useContext(MenuContext);
  const [cart, setCart] = useContext(CartContext);
  const [total, setTotal] = useContext(TotalContext);

  const fetchData = async () => {
    const result = await axios("https://feolas.herokuapp.com/menu");

    setMenu(result.data);
  };

  useEffect(() => {
    localStorage.setItem("cart", JSON.stringify(cart));
  });

  useEffect(() => {
    fetchData();
    handleTotal();
  }, []);

  const handleTotal = () => {
    const values = cart.map((i) => i.val * i.price);
    setTotal(values.reduce((a, b) => a + b, 0));
  };

  const handleSubmitCartItem = (e) => {
    const newItem = {
      name: e.name,
      price: e.price,
      val: 1,
      id: e._id,
    };

    handlePostDataCart(newItem);
  };

  const handlePostDataCart = (e) => {
    if (cart.some((i) => i.name === e.name)) {
      console.log(
        "Это блюдо уже в корзине! Чтобы увеличить колличество - перейдите в корзину ;)"
      );
    } else {
      setCart((curr) => [...curr, e]);
    }
  };

  useEffect(() => {
    cart ? handleTotal() : console.log("cart is empty");
  }, [cart]);

  return (
    <Router>
      <div className="App">
        <Navbar />
        <SideNav />
        <div className="body-wrapper">
          
          <div className="content">
            <Route path="/" exact component={Homepage} />
            <Route path="/admin" component={Admin} />
            <Route
              path="/firstdishes"
              render={(props) => (
                <Soupes {...props} cart={handleSubmitCartItem} />
              )}
            />
            <Route
              path="/seconddishes"
              render={(props) => (
                <Seconds {...props} cart={handleSubmitCartItem} />
              )}
            />
            <Route
              path="/salades"
              render={(props) => (
                <Salades {...props} cart={handleSubmitCartItem} />
              )}
            />
            <Route
              path="/deserts"
              render={(props) => (
                <Deserts {...props} cart={handleSubmitCartItem} />
              )}
            />
            <Route
              path="/kids"
              render={(props) => (
                <Kids {...props} cart={handleSubmitCartItem} />
              )}
            />
          </div>
        </div>
      </div>
    </Router>
  );
}

export default App;
