import React, { useContext } from 'react';
import "../styles/seconds.css";
import { MenuContext } from '../MenuContext';

const Seconds = (props) => {

    const [menu] = useContext(MenuContext);
  
    return (
        <div className="dish-container">
           
            <div className="cards-wrap">
            {menu.length ? menu.filter((i) => i.category ==="seconddishes").map((item) => {
                return(
            <div className="dish-card" key={item._id}>
                
                <img src={item.photo_url} alt="dish" className="dick-pic" />
                <h3 className="dish-name" >{item.name}</h3>
                <span className="dish-desc">{item.desc}</span>
                <div className="foot">
                <span className="dish-price">{item.price} rub</span>
                <span className="add-to-cart" id={item._id} onClick={() => {
                    props.cart(item);
                } 
                }></span>
                </div>
                

            </div>) 
             }) : console.log('loading data')
            }
            </div>
        </div>
        
    )
}

export default Seconds
