import React, { useState } from "react";

export const CartAddContext = React.createContext();

export const CartAddProvider = (props) => {
  const [name, setName] = useState("");
  const [price, setPrice] = useState("");
  const [val, setVal] = useState(1);
  return (
    <CartAddContext.Provider
      value={[name, setName, price, setPrice, val, setVal]}
    >
      {props.children}
    </CartAddContext.Provider>
  );
};
