import React, { useEffect } from 'react';
import imgone from '../img/tomyam.jpg';
import imgtwo from '../img/sudak.jpg';
import imgthree from '../img/chukka.jpg'
import '../styles/slideshow.css'

const SlideShow = () => {
    
    const handleSlides = () => {
        let val = 0;
        rate();

        function rate() {
            const slides = document.querySelectorAll('.slide-box');
            slides.forEach(i => i.style.display = "none");

            slides[val].style.display = "block";
            if (val > 0) {slides[val-1].style.display="none";}
            
            val++;
            
            if (val > slides.length-1) {val = 0}

            setTimeout(() => rate() , 5000)
        }  

    }

    useEffect(() => {
        
        handleSlides()
    }, [])

    return (
        <div className="slide-container">
      
        <div className="slide-box fade">
            <img src={imgone} alt="first" className="slide" />
        </div>
        <div className="slide-box fade">
            <img src={imgtwo} alt="first" className="slide" />
        </div>
        <div className="slide-box fade">
            <img src={imgthree} alt="first" className="slide" />
        </div>
      
        </div>
    )
}

export default SlideShow
